<template>
  <div id="app">
    <v-app>
      <Header />
      <main>
        <v-container>
          <router-view/>
        </v-container>
      </main>
    </v-app>
  </div>
</template>

<script>
import Header from '@/components/ContainerComponents/Header.vue'
export default {
  name: 'App',
  async created () {
    this.$store.dispatch('requestRecipes')
  },
  components: {
    Header
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* text-align: center; */
  color: #2c3e50;
  margin-top: 60px;
}
</style>



